@import 'index.sass';

//elements -> ids -> classes
body {
	background-color: $background;
	margin: 0;
	* {
		outline: none;
		font-family: Consolas, Ubuntu Mono, Courier New,  Andale Mono, monospace;
		color: $uno-1;
		box-sizing: border-box; }
	a {
		color: $duo-1; }
	u {
		text-decoration-color: $duo-2; } }

.wrapper {
	//display: grid
	grid-template-columns: 285px 1fr;
	display: none; }

.title {
	color: $duo-2;
	margin: 0 0 0 10px; }

.hidden {
	display: none; }

.active {
	display: block !important; }

.ascii {
	text-decoration: none !important;
	*:hover {
		color: $duo-1; } }

input {
	border: 1px $uno-4 solid;
	border-radius: 5px;
	background-color: $background;
	padding: 15px;
	margin: 15px;
	width: 90%;
	box-sizing: border-box;
	font-size: 1em;
	color: $uno-1; }

.tech {
	display: flex;
	p {
		margin: 0 10px 0 20px; }
	a {
		margin: 0 10px 0 0; } }

.name {
	display: flex;
	align-items: center; }

.hover {
	position: relative;
	display: inline-flex;
	justify-content: center;
	span {
		visibility: hidden;
		font-weight: bold;
		width: max-content;
		text-align: center;
		background-color: $background;
		border-radius: 5px;
		padding: 5px;
		margin: 5px;
		border: 1px $uno-4 solid;
		position: absolute;
		z-index: 2;
		bottom: 125%; }
	&:hover span {
		visibility: visible; } }

table {
	th {
		text-align: left; }
	td, th {
		padding-right: 50px;
		&:before {
			content: '|';
			margin-right: 25px;
			text-decoration: none; } } }

.team {
	p {
		margin-bottom: 0; }
	ul, li p {
		margin: 0; } }

.hover p {
	padding: 2px;
	background-color: $uno-5;
	color: $duo-1;
	position: absolute; }

.container {
	display: inline; }

.cli {
	position: fixed;
	bottom: 20px;
	margin: 0; }

.terms {
	position: fixed;
	bottom: 70px;
	margin: 0; }

.cont {
	width: 100%; }
